import React from 'react'
import Link from 'gatsby-link'

const AboutPage = () => (
  <article>
    <header>
      <h1>About</h1>
    </header>
    <div className="body">
      <p>Hello! I'm <a href="//markanthonyuy.com">Mark Anthony Uy</a> a Senior Front-End developer living in the Philippines. As a hobby I like creating web apps using new frontend technologies as listed in my personal projects list at the sidebar. You can follow me on <a href="//twitter.com/markanthonyuy">twitter</a> the only social media I'm active.</p>
      <p>This is my personal blog. Here I'll post all the interesting events happened to me that I want to share with everyone. I don't like drama so only expect feel good post. :) So sit back relax and enjoy reading!</p>
      <p>This blog is built on <a href="https://www.gatsbyjs.org/">Gatsbyjs</a></p>
      <h2>Credits</h2>
      <ul>
        <li><a href="https://codepen.io/Mark_Bowley/pen/xEbuI">Clouds</a></li>
        <li><a href="https://codepen.io/Hackroro/pen/ByrKLZ">Sun</a></li>
        <li><a href="https://codepen.io/Christopherallen/pen/Clajw">Mountain</a></li>
        <li><a href="https://codepen.io/orchard/pen/CizvD">Moon</a></li>
        <li><a href="https://codepen.io/saransh/pen/BKJun">Stars</a></li>
      </ul>
    </div>
  </article>
)

export default AboutPage



